import {request} from './request';

// 修改密码   old_password  password  password_confirmation
export function changeSecret(data){
    return request({
        url:'/api/auth/password/update',
        method:'post',
        data
    })
}

// 获取邮箱验证码   email
export function getChangeEmailCode(data){
    return request({
        url:'/api/auth/email/code',
        method:'post',
        data
    })
}

// 更换邮箱         code  email
export function changeEmail(data){
    return request({
        url:'/api/auth/email/update',
        method:'patch',
        data
    })
}

// 获取手机验证码   phone
export function getBindPhoneCode(data){
    return request({
        url:'/api/auth/phone/code',
        method:'post',
        data
    })
}

// 绑定手机        code  phone
export function bindPhone(data){
    return request({
        url:'/api/auth/phone/update',
        method:'patch',
        data
    })
}

// 获取找回密码的验证码   email
export function getBackSecretCode(data){
    return request({
        url:'/api/auth/reset/password/email/code',
        method:'post',
        data
    })
}

// 找回密码        code  email  password  password_confirmation
export function backSecret(data){
    return request({
        url:'/api/auth/reset/password/email',
        method:'post',
        data
    })
}