<template>
    <!--设置内容-->
    <div id="_register">
        <!--设置标题栏的后退功能-->
        <div class="flex justify-content-start w-100">
            <div class="d-flex justify-content-between pb-2 pb-sm-3">
                <div class="txt-subtitle txt-subtitle-space  px-2 px-sm-3 " style="font-weight:bold"
                     @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px;color:#1f1f1f"></i>
                    <span>设置</span>
                </div>
            </div>
        </div>
        <!--设置小标题栏-->
        <div class="_register-title d-flex justify-content-center pb-2 pb-sm-3 w-100">
            <div class=" d-flex justify-content-between w-100" style="min-width:380px;max-width:780px;">
                <ul class="list-unstyled _shopRow col-12   d-flex justify-content-around align-items-center txt-subbody py-3">
                    <li class="mycol active  d-inline">
                        <div class=" d-inline-block  mx-auto" @click="onChangeTab(0)">
                            <i class="iconfont icon-bangdingshouji d-block"></i>
                            <span class="item w-100">绑定手机</span>
                        </div>
                        <hr class="link-line right-link-line">               <!--右侧连接线-->
                    </li>
                    <li class="mycol  d-inline">
                        <hr class="link-line left-link-line">               <!--左侧连接线-->
                        <div class=" d-inline-block  mx-auto" @click="onChangeTab(1)">
                            <i class="iconfont icon-xiugaimima d-block"></i>
                            <span class="item w-100">修改密码</span>
                        </div>
                        <hr class="link-line right-link-line">
                    </li>
                    <li class="mycol  d-inline">
                        <hr class="link-line left-link-line">               <!--左侧连接线-->
                        <div class=" d-inline-block  mx-auto" @click="onChangeTab(2)">
                            <i class="iconfont icon-youxiang d-block"></i>
                            <span class="item w-100">更换邮箱</span>
                        </div>
                        <hr class="link-line right-link-line">
                    </li>
                    <li class="mycol  d-inline">
                        <hr class="link-line left-link-line">               <!--左侧连接线-->
                        <div class=" d-inline-block  mx-auto" @click="onChangeTab(3)">
                            <i class="iconfont icon-zhaohuimima1 d-block"></i>
                            <span class="item w-100">找回密码</span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <!--表单内容栏-->
        <div class="_register-container">
            <div class="box">
                <!--绑定手机表单-->
                <div class="box-item">
                    <div class="_register-box d-inline-block px-3 mb-3">
                        <div class="txt-subtitle py-3" style="font-weight:bold">绑定手机</div>
                        <div class="_form-box">
                            <div class="_form-box1  active   flex-column justify-content-between align-items-center py-4 px-2">
                                <input type="text" class=" py-2 my-1 " placeholder="手机号" v-model="bindphone.phone"
                                       minlength="11" maxlength="11"
                                       name="phone" title="手机号长度等于11位"  style="text-indent:8px;width:82%;" required
                                       />
                                <input type="submit" value="获取验证码" @click="onSubmitForm(0,0)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box2 flex-column justify-content-between align-items-center   py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="手机号" v-model="bindphone.phone"
                                        minlength="11" maxlength="11"
                                       name="phone" title="手机号长度等于11位"  style="text-indent:8px;width:82%;" required/>
                                <div class=" my-2  d-flex flex-row justify-content-start align-items-center" style="width:82%;">
                                    <span class="w-25 text-left" style="text-align:left">验证码：</span>
                                    <input type="text" class=" w-75 py-2"  v-model="bindphone.code"  name="code"
                                             minlength="4" maxlength="4" style="text-indent:8px;width:82%;" required/>
                                </div>
                                <input type="submit" value="提交" @click="onSubmitForm(0,1)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box3 flex-column justify-content-between align-items-center pb-2 px-2">
                                <div class="d-flex flex-column justify-content-center align-items-center"
                                     style="width:300px;height:300px">
                                    <i class="iconfont icon-chenggong" style="font-size:60px;color:red; "></i>
                                    <span class="text-danger text-subbody mt-3"
                                          style="letter-spacing:1px;font-weight:bold;">绑定手机成功!</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--修改密码表单-->
                <div class="box-item">
                    <div class="_register-box d-inline-block px-3 mb-3">
                        <div class="txt-subtitle py-3" style="font-weight:bold">修改密码</div>
                        <div class="_form-box">
                            <div class="_form-box2   active  flex-column justify-content-between align-items-center py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="原密码" v-model="secret.old_password"
                                       name="old_password"
                                       title="原密码长度大于等于6位"
                                       style="text-indent:8px;width:82%;" required/>
                                <input type="password" class=" py-2 my-1" placeholder="新密码" v-model="secret.password"
                                       name="password" title="密码不应该小于6位"
                                       style="text-indent:8px;width:82%;" required/>
                                <input type="password" class=" py-2 my-1" placeholder="确认密码"
                                       v-model="secret.password_confirmation" name="password_confirmation"
                                       title="两次密码必需相同"
                                       style="text-indent:8px;width:82%;" required/>
                                <input type="submit" value="提交" @click="onSubmitForm(1)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box3  flex-column justify-content-between align-items-center pb-2 px-2">
                                <div class="d-flex flex-column justify-content-center align-items-center"
                                     style="width:300px;height:300px">
                                    <i class="iconfont icon-chenggong" style="font-size:60px;color:red; "></i>
                                    <span class="text-danger text-subbody mt-3"
                                          style="letter-spacing:1px;font-weight:bold;">修改密码成功!</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--更换邮箱表单-->
                <div class="box-item">
                    <div class="_register-box d-inline-block px-3 mb-3">
                        <div class="txt-subtitle py-3" style="font-weight:bold">更换邮箱</div>
                        <div class="_form-box">
                            <div class="_form-box1  active   flex-column justify-content-between align-items-center py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="新邮箱" v-model="changeemail.email"   minlength="11"
                                       name="email" title="邮箱长度不小于11位"  style="text-indent:8px;width:82%;" required />
                                <input type="submit" value="获取验证码" @click="onSubmitForm(2,0)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box2 flex-column justify-content-between align-items-center   py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="新邮箱" v-model="changeemail.email" minlength="11"
                                       name="email" title="邮箱长度不小于11位"  style="text-indent:8px;width:82%;" required/>
                                <div class=" my-2  d-flex flex-row justify-content-start align-items-center" style="width:82%;">
                                    <span class="w-25 text-left" style="text-align:left">验证码：</span>
                                    <input type="text" class=" w-75 py-2"  v-model="changeemail.code"  name="code"
                                           minlength="4" maxlength="4" style="text-indent:8px;width:82%;" required/>
                                </div>
                                <input type="submit" value="提交" @click="onSubmitForm(2,1)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box3 flex-column justify-content-between align-items-center pb-2 px-2">
                                <div class="d-flex flex-column justify-content-center align-items-center"
                                     style="width:300px;height:300px">
                                    <i class="iconfont icon-chenggong" style="font-size:60px;color:red; "></i>
                                    <span class="text-danger text-subbody mt-3"
                                          style="letter-spacing:1px;font-weight:bold;">更换邮箱成功!</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--找回密码表单-->
                <div class="box-item">
                    <div class="_register-box d-inline-block px-3 mb-3">
                        <div class="txt-subtitle py-3" style="font-weight:bold">找回密码</div>
                        <div class="_form-box">
                            <div class="_form-box1  active   flex-column justify-content-between align-items-center py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="邮箱" v-model="backsecret.email"   minlength="11"
                                       name="email" title="邮箱长度不小于11位"  style="text-indent:8px;width:82%;" required  />
                                <input type="submit" value="获取验证码" @click="onSubmitForm(3,0)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box2 flex-column justify-content-between align-items-center   py-4 px-2">
                                <input type="text" class=" py-2 my-1" placeholder="邮箱" v-model="backsecret.email"   minlength="11"
                                       name="email" title="邮箱长度不小于11位"  style="text-indent:8px;width:82%;" required  />
                                <div class=" my-2  d-flex flex-row justify-content-start align-items-center" style="width:82%;">
                                    <span class="w-25 text-left" style="text-align:left">验证码：</span>
                                    <input type="text" class=" w-75 py-2"  v-model="backsecret.code"  name="code"
                                           minlength="4" maxlength="4" style="text-indent:8px;width:82%;" required/>
                                </div>
                                <input type="password" class=" py-2 my-1" placeholder="新密码" v-model="backsecret.password"   minlength="6"
                                       name="password" title="密码长度不小于6位"  style="text-indent:8px;width:82%;" required  />
                                <input type="password" class=" py-2 my-1" placeholder="确认密码" v-model="backsecret.password_confirmation"   minlength="6"
                                       name="password_confirmation" title="确认密码长度不小于6位"  style="text-indent:8px;width:82%;" required  />
                                <input type="submit" value="提交" @click="onSubmitForm(3,1)" class="text-white py-2 my-2 "
                                       style="background:red;width:82%;">
                            </div>
                            <div class="_form-box3 flex-column justify-content-between align-items-center pb-2 px-2">
                                <div class="d-flex flex-column justify-content-center align-items-center"
                                     style="width:300px;height:300px">
                                    <i class="iconfont icon-chenggong" style="font-size:60px;color:red; "></i>
                                    <span class="text-danger text-subbody mt-3"
                                          style="letter-spacing:1px;font-weight:bold;">找回密码成功!</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery';
    import {ElMessage} from 'element-plus';
    import {getRegister} from 'network/user'
    import {
        changeSecret, getChangeEmailCode, changeEmail, getBindPhoneCode,
        bindPhone, getBackSecretCode, backSecret
    } from 'network/settings'

    import {ref, reactive, toRefs, onMounted} from 'vue'
    import {useRouter,useRoute} from 'vue-router'

    export default {
        name: "Settings",
        setup() {
            const router = useRouter();
            const route = useRoute();
            // 用户的输入信息
            const userinfo = reactive({
                secret: {                   // 修改密码
                    old_password: "",
                    password: '',
                    password_confirmation: '',
                },
                bindphone:{                // 绑定手机
                    phone:'',
                    code:''
                },
                changeemail:{              // 更换邮箱
                    email:'',
                    code:''
                },
                backsecret:{               // 找回密码
                    email:'',
                    code:'',
                    password:'',
                    password_confirmation:''
                }
            });

            let activeItemsIndex = ref(0);



            //  eduwork     123456   12345600@qq.com
            //  smallcat    123456   778899@qq.com
            // 绑定手机    176****
            // 修改密码    1234567
            // 更换邮箱    227697****@qq.com   ？? 服务器检测，邮箱不存在
            // 找回密码    12345678            ？？ 原来的邮箱本不存在，收不到验证码

            onMounted(() => {
                initAllData();           // 清空所有表单数据
                var items = parseInt(route.query.items);
                if(items>3 || items < 0 || isNaN(items)){     // NaN 是非数值，NaN!=NaN, NaN!=undefined
                    items = 0;
                }
                activeItemsIndex.value = items;
                console.log('当前的选项是',activeItemsIndex.value);
                onChangeTab(activeItemsIndex.value);
            });

            // 初始化所有表单数据
            const initAllData = () => {
                // 清空密码的表单数据
                userinfo.secret.old_password = '';
                userinfo.secret.password = '';
                userinfo.secret.password_confirmation = '';
                // 清空绑定手机的表单数据
                userinfo.bindphone.phone = '';
                userinfo.bindphone.code = '';
                // 清空更换邮箱的表单数据
                userinfo.changeemail.email = '';
                userinfo.changeemail.code = '';
                // 清空找回密码的表单数据
                userinfo.backsecret.email = '';
                userinfo.backsecret.code = '';
                userinfo.backsecret.password = '';
                userinfo.backsecret.password_confirmation = '';
            };

            // 切换标题栏
            const onChangeTab = (index) => {
                var node = $('._register-title ul._shopRow li:eq(' + index + ')');
                var scrollN = $('._register-container');
                var scrollnode = $('._register-container .box');
                var scrollnodeLast = $('._register-container .box div').last();
                if (!node.hasClass('active')) {
                    node.addClass('active').siblings('li').removeClass('active');
                    activeItemsIndex.value = index;
                    console.log(scrollN[0], scrollnode[0], scrollnodeLast[0]);
                    // 盒子在容器中，容器的移动属性
                    /*if(scrollN[0].scrollLeft>=1200)
                        scrollN[0].scrollLeft = 0;
                    else
                        scrollN[0].scrollLeft += 400;*/
                    // 盒子在容器中，移动盒子的显示
                    var stepWidth = (index % 4) * 400;
                    scrollnode.css('transform', 'translateX(-' + stepWidth + 'px)');
                    initAllData()                                                    // 清空原来的数据
                }
            };


            // 简单验证邮箱是否正确
            const onVerifySimplyEmail = (newString) => {
                if(newString.indexOf('@')!==-1 && newString.endsWith('.com')!==false  && newString.lastIndexOf('@')<newString.lastIndexOf('.com')-2){
                    return true;
                }else{
                    return '请输入正确的邮箱';
                }
            };


            // ========提交表单数据========
            const onSubmitForm = (index,flag=null) => {
                if (index === 0) {
                    console.log('当前是绑定手机');
                    console.log(userinfo.bindphone);

                    if(flag!==null && flag === 0){
                        var res_phone1 = verifyBindPhone1();
                        console.log('res_phone1', res_phone1);
                        if (res_phone1 === true) {
                            console.log('开始获取手机验证码');
                            onBindPhone1();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_phone1);
                        }
                    }else if(flag!==null && flag === 1){

                        var res_phone2 = verifyBindPhone2();
                        console.log('res_phone2', res_phone2);
                        if (res_phone2 === true) {
                            console.log('开始绑定手机');
                            onBindPhone2();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_phone2);
                        }
                    }

                } else if (index === 1) {
                    console.log('当前是修改密码');
                    console.log(userinfo.secret);
                    var res_secret = verifyChangeSecret();
                    console.log('res_secret', res_secret);
                    if (res_secret === true) {
                        console.log('开始修改密码');
                        onChangeSecret();
                    } else {
                        console.log('表单数据错误');
                        ElMessage.error(res_secret);
                    }

                }else if (index === 2) {
                    console.log('当前是更换邮箱');
                    console.log(userinfo.changeemail);

                    if(flag!==null && flag === 0){

                        var res_email1 = verifyChangeEmail1();
                        console.log('res_email1', res_email1);
                        if (res_email1 === true) {
                            console.log('开始获取邮箱验证码');
                            onChangeEmail1();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_email1);
                        }
                    }else if(flag!==null && flag === 1){
                        var res_email2 = verifyChangeEmail2();
                        console.log('res_email2', res_email2);
                        if (res_email2 === true) {
                            console.log('开始更换邮箱');
                            onChangeEmail2();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_email2);
                        }
                    }

                }else if (index === 3) {
                    console.log('当前是找回密码');
                    console.log(userinfo.backsecret);

                    if(flag!==null && flag === 0){

                        var res_backemail1 = verifyBackSecret1();
                        console.log('res_backemail1', res_backemail1);
                        if (res_backemail1 === true) {
                            console.log('开始获取（找回）邮箱验证码');
                            onBackSecret1();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_backemail1);
                        }
                    }else if(flag!==null && flag === 1){

                        var res_backemail2 = verifyBackSecret2();
                        console.log('res_backemail2', res_backemail2);
                        if (res_backemail2 === true) {
                            console.log('开始找回密码');
                            onBackSecret2();
                        } else {
                            console.log('表单数据错误');
                            ElMessage.error(res_backemail2);
                        }
                    }

                }
            };


            //  ---- 修改密码--------
            // 检测修改密码表单数据的正确性
            const verifyChangeSecret = () => {
                if (userinfo.secret.old_password.length === 0) {
                    return '原密码不能为空';
                } else if (userinfo.secret.password.length === 0) {
                    return '新密码不能为空';
                } else if (userinfo.secret.password_confirmation.length === 0) {
                    return '确认密码不能为空';
                } else if (userinfo.secret.password_confirmation !== userinfo.secret.password) {
                    return '两次输入的密码不一致';
                }
                return true;
            };

            // 修改密码
            const onChangeSecret = () => {
                changeSecret(userinfo.secret).then(res => {
                    console.log('secret-data', res);
                    if (res.status === 204) {
                        console.log('修改密码成功！');
                        ElMessage.success('修改密码成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box3').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                        initAllData();                                          //清空所有表单数据
                    } else {
                        console.log('修改密码失败！');
                        ElMessage.error('修改密码失败！');
                    }
                }).catch(err => {
                    console.log('setting-secret-err', err);
                    ElMessage.error('修改密码失败！请重试');
                });
            };


            //  ---- 绑定手机----------
            // 检测修改密码表单数据的正确性
            const verifyBindPhone1 = () => {
                if (userinfo.bindphone.phone.length === 0) {
                    return '手机号码不能为空';
                }
                return true;
            };
            const verifyBindPhone2 = () => {
                if (userinfo.bindphone.phone.length === 0) {
                    return '手机号码不能为空';
                } else if (userinfo.bindphone.code.length === 0) {
                    return '新密码不能为空';
                }
                return true;
            };

            // 绑定手机：获取验证码,绑定手机号
            const onBindPhone1 = () => {
                var data = {phone:userinfo.bindphone.phone};
                getBindPhoneCode(data).then(res => {
                    console.log('phone-data', res);
                    if (res.status === 204) {
                        console.log('获取验证码成功！');
                        ElMessage.success('获取验证码成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box2').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                    } else {
                        console.log('获取验证码失败！');
                        ElMessage.error('获取验证码失败！');
                    }
                }).catch(err => {
                    console.log('setting-bindphone1-err', err);
                    ElMessage.error('获取验证码失败！请重试');
                });
            };
            const onBindPhone2 = () => {
                // 验证码是数字，而不是字符串;
                bindPhone(userinfo.bindphone).then(res => {
                    console.log('bindphone2-data', res);
                    if (res.status === 204) {
                        console.log('绑定手机成功！');
                        ElMessage.success('绑定手机成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box3').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                        initAllData();                                          //清空所有表单数据
                    } else {
                        console.log('绑定手机失败！');
                        ElMessage.error('绑定手机失败！');
                    }
                }).catch(err => {
                    console.log('setting-bindhpone2-err', err);
                    ElMessage.error('绑定手机失败！请重试');
                });
            };


            //  ---- 更换邮箱----------
            // 检测更换邮箱表单数据的正确性
            const verifyChangeEmail1 = () => {
                if (userinfo.changeemail.email.length === 0) {
                    return '邮箱不能为空';
                } else if (userinfo.changeemail.email.length < 11) {
                    return '标准邮箱长度不小于11';
                }
                var verifyEmail = onVerifySimplyEmail(userinfo.changeemail.email);
                if(verifyEmail!==true){
                    return verifyEmail;
                }
                return true;
            };
            const verifyChangeEmail2 = () => {
                if (userinfo.changeemail.email.length === 0) {
                    return '邮箱不能为空';
                } else if (userinfo.changeemail.email.length < 11) {
                    return '标准邮箱长度不小于11';
                }else if (userinfo.changeemail.code.length === 0) {
                    return '验证码不能为空';
                }

                var verifyEmail = onVerifySimplyEmail(userinfo.changeemail.email);
                if(verifyEmail!==true){
                    return verifyEmail;
                }
                return true;
            };

            // 更换邮箱：获取验证码,更换邮箱
            const onChangeEmail1 = () => {
                var data = {email:userinfo.changeemail.email};
                getChangeEmailCode(data).then(res => {
                    console.log('email-data', res);
                    if (res.status === 204) {
                        console.log('获取验证码成功！');
                        ElMessage.success('获取验证码成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box2').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                    } else {
                        console.log('获取验证码失败！');
                        ElMessage.error('获取验证码失败！');
                    }
                }).catch(err => {
                    console.log('setting-changeemail1-err', err);
                    ElMessage.error('获取验证码失败！请重试');
                });
            };
            const onChangeEmail2 = () => {
                console.log("再次查看表单数据changeemail",userinfo.changeemail);
                backSecret(userinfo.changeemail).then(res => {
                    console.log('changeemail2-data', res);
                    if (res.status === 204) {
                        console.log('更换邮箱成功！');
                        ElMessage.success('更换邮箱成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box3').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                        initAllData();                                          //清空所有表单数据
                    } else {
                        console.log('更换邮箱失败！');
                        ElMessage.error('更换邮箱失败！');
                    }
                }).catch(err => {
                    console.log('setting-changeemail2-err', err);
                    ElMessage.error('更换邮箱失败！请重试');
                });
            };


            //  ---- 找回密码----------
            // 检测找回密码表单数据的正确性
            const verifyBackSecret1 = () => {
                if (userinfo.backsecret.email.length === 0) {
                    return '邮箱不能为空';
                }else if (userinfo.backsecret.email.length < 11) {
                    return '标准邮箱长度不小于11';
                }
                var verifyEmail = onVerifySimplyEmail(userinfo.backsecret.email);
                if(verifyEmail!==true){
                    return verifyEmail;
                }
                return true;
            };
            const verifyBackSecret2 = () => {
                if (userinfo.backsecret.email.length === 0) {
                    return '邮箱不能为空';
                } else if (userinfo.backsecret.email.length < 11) {
                    return '标准邮箱长度不小于11';
                }else if (userinfo.backsecret.code.length === 0) {
                    return '验证码不能为空';
                } else if (userinfo.backsecret.password.length === 0) {
                    return '新密码不能为空';
                } else if (userinfo.backsecret.password_confirmation.length === 0) {
                    return '确认密码不能为空';
                } else if (userinfo.backsecret.password_confirmation !== userinfo.backsecret.password) {
                    return '两次输入的密码不一致';
                }

                var verifyEmail = onVerifySimplyEmail(userinfo.backsecret.email);
                if(verifyEmail!==true){
                    return verifyEmail;
                }
                return true;
            };

            // 找回密码：获取验证码,找回密码
            const onBackSecret1 = () => {
                var data = {email:userinfo.backsecret.email};
                getBackSecretCode(data).then(res => {
                    console.log('email-data', res);
                    if (res.status === 204) {
                        console.log('获取验证码成功！');
                        ElMessage.success('获取验证码成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box2').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                    } else {
                        console.log('获取验证码失败！');
                        ElMessage.error('获取验证码失败！');
                    }
                }).catch(err => {
                    console.log('setting-backsecret1-err', err);
                    ElMessage.error('获取验证码失败！请重试');
                });
            };
            const onBackSecret2 = () => {
                backSecret(userinfo.backsecret).then(res => {
                    console.log('backsecret2-data', res);
                    if (res.status === 204) {
                        console.log('找回密码成功！');
                        ElMessage.success('找回密码成功！');
                        $('.box div.box-item:eq(' + activeItemsIndex.value + ') ._form-box ._form-box3').addClass('active').siblings('div').removeClass('active');         // 显示下一个界面
                        initAllData();                                          //清空所有表单数据
                    } else {
                        console.log('找回密码失败！');
                        ElMessage.error('找回密码失败！');
                    }
                }).catch(err => {
                    console.log('setting-backsecret2-err', err);
                    ElMessage.error('找回密码失败！请重试');
                });
            };


            return {...toRefs(userinfo), onChangeTab, onSubmitForm};
        },
        components: {}
    }
</script>

